<template>
  <div id="preloader"></div>
</template>

<script>
import { select } from "../utils/Functions";
export default {
  name: "Preload",
  mounted() {
    let preloader = select("#preloader");
    if (preloader) {
      window.addEventListener("load", () => {
          preloader.remove();
      });
    }
  },
};
</script>

<style lang="scss">
@keyframes animate-preloader {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

#preloader {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 9999;
  overflow: hidden;
  background: #37517e;
  &:before {
    content: "";
    position: fixed;
    top: calc(50% - 30px);
    left: calc(50% - 30px);
    border: 6px solid #37517e;
    border-top-color: #fff;
    border-bottom-color: #fff;
    border-radius: 50%;
    width: 60px;
    height: 60px;
    animation: animate-preloader 1s linear infinite;
  }
}
</style>
